



.row{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
 .title-header-bakhour {
  font-size: 1em;
  font-weight: bold;
  font-size:1.3em;
  padding-top:10%;
  text-align: center;
 }
 #row-pic{
width:100px ;
 }
 .title-header-bakhour-skincare {
  font-size: 1.3em;
  padding-left: 5%;
  padding-top:0;
  font-weight: bold;
 }
 .title-header-bakhour1 {
  font-size: 1.5em;
  font-weight: 600;
  padding-left: 10px;
  padding-top: 10px; 
  padding-bottom: 0;
 }

 .header {
  position: sticky;
  position: -webkit-sticky;
   width: 100%;
  z-index: 800;
 }
 .top-alert {
  
  padding: 8px;
  text-align: center;
 }
 .top-alert-text {
  font-size: 0.6em;
  text-align: center;
  justify-content: center;
  font-weight: 550;
 }
 a {
  color: inherit;
 }
 a:hover { 
 color: blue;
 }
 .menu {
  display: flex;
  position: relative; 
  -webkit-box-align: center;
  align-items: center;
  height: 48px;
  z-index: 800;
 
 }
 .menu>div {
  max-width: 1270px;
  height: inherit;
  min-width: 40px;
  margin: 1%;
  justify-content: center;
  padding: 0px 4px;
  font-size: 0.8em;
 }
 .menu>div:nth-child(3) {
  margin: 0 auto;
  max-width: 130px;
  min-width: 90px;
 }
 .option {
  display: flex;
  position: relative; 
  -webkit-box-align: center;
  align-items: center;
  justify-content: center;
  height: 39px;
 
 }
 .option>div {
   max-width: 150px;
  height: inherit;
  min-width: 40px;
  margin: 1%;
  justify-content: center;
  padding: 10px;
  font-size: 0.8em;
  border-radius: 3px;
  transition: all .3s ease;
 }
 .option>div:hover {
  
  cursor: pointer;
  border: 1px solid grey;
 }
 @media (max-width: 768px) {
  .option {
  overflow-x: visible;
  display: block;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  height: auto;
 }
 .option>div {
  text-align: center;
   justify-content: center;
  margin: 0 auto;
 }
 }
  
 #icon {
  height: inherit;
  width:20px;
  justify-content: center;
  text-align: center;
  margin-bottom: 2%;
 }
 #logo {
  width: 90%;
  height: 28px;
  margin-top: 7%;
 }
 .container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
 }
 .section {
  border: 1px solid;
 }
 @media (max-width: 768px) {
  .container {
  grid-template-columns: none;
 }
  .text-box {
  padding-bottom: 10vh;
 }
  #grid-pic {
  max-height: 580px;
 }
 }
 #grid-pic {
  height: 90vh;
  width: 100%;
  transition: .3s ease;
 }
 .text-box { 
  height: auto;
  width: 90%;
  margin: 0 auto;
  padding-top: 15vh;
 }
 .shopnow-button {
  height: 70px;
  width: 270px;
  background-color: #121212;
  color: white;
  font-size: 1em;
  font-weight: bold; 
  border: none; 
 border-radius: 5px;
  margin: 0 auto; 
  position: relative;
  display: block;
  transition: all .3s ease;
 }
 .shopnow-button:hover {
  background-color: rgba(0,0,0,0.7);
  cursor: pointer;
 }
 .wrapper {
  padding: 5px;
  max-width: 960px;
  width: 95%;
  margin: 20px auto;
 }
 header {
  padding: 0 15px;
 }
 .wrapper-text-box {
  width: 90%;
  margin: 10px;
  display: inline-block;
 } 
 .col {
  float: left;
  width: 60%;
  height: 400px; 
  margin: 33px;
  justify-content: center;
  text-align: center;
  text-align: left;
  font-size: 0.8em;
 }
 
 .row:after {
  content: "";
  clear: both;
  display: table;
  
 }
 @media screen and (max-width: 600px) {
  .col{
  width:45%;
  height: 330px;
  margin-bottom: 5px; 
  margin-left: 15px;
  margin-right: 0;
 }
 }
 #row-pic {
  height: 80%;
  width: 100%;
  
 }
 