.lp {
  max-width: 960px;
  margin: auto;
  min-height: 600px;
  box-shadow: 0px 0px 16px 1px rgba(0,0,0,0.2);
  background: #ffffff; 
  background: radial-gradient(
    45% 85% at 65% 65%,
    rgba(13, 58, 216, 0.8) 0%,
    rgba(0, 0, 0, 0) 100%
  ),
  radial-gradient(35% 70% at 35% 50%, #9d0303 0%, rgba(0, 0, 0, 0) 79.09%),
  #000000;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f6f5f2',GradientType=1 ); 
  -webkit-transform: scale(0.9); 
          transform: scale(0.9);
}
/*Header*/
.lp__header {
  width: 88%; 
  margin: auto;
  text-align: center;
  padding: 32px 0;
}
/* h1 {
  font-size: 36px;
  margin: 16px 0;
  font-family: $font-serif;
  font-weight: 900;
} */
.lp__header p {
  /* font-family: $font-serif; */
  font-size: 14px;
}
/*Grid*/
.lp__grid {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(2, 1fr);
  width: 88%;
  margin: auto;
}

.lp__gridItem__image {
  /* background: #ccc; */
  grid-row: 1/4;
  grid-column: 1/3;
  height: 340px;
  background: url('/Image/car2.jpg')no-repeat center center;
}
.lp__gridItem__text {
  grid-row: 3/5;
  grid-column: 2/3;
  background: radial-gradient(
      45% 85% at 65% 65%,
      rgba(13, 58, 216, 0.8) 0%,
      rgba(0, 0, 0, 0) 100%
    ),
    radial-gradient(35% 70% at 35% 50%, #9d0303 0%, rgba(0, 0, 0, 0) 79.09%),
    #000000;
  margin: 32px 0;
  padding: 0 16px;
  color: white;
}
.lp__gridItemText-sm {
  font-size: 12px;
  color: white;
}
.lp__gridItemText-sm-bw {
  color:white;
}
.lp__gridItemText-md {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.54;
  /* font-family: $font-serif; */
  font-size: 13px;
}



.contact-us-bakhour {
 
  height:100vh;
  display: flex;
  justify-content: center;
  align-items:center;
  padding-top: 50px;
  margin-top: 20px;
}

.form-bakhour {
  padding-left:25px;
  padding-right:25px;
  padding-top:60px;
  margin: 12px auto;
  border: 2px solid #ccc;
  border-radius: 20px;
  position:relative;
  z-index:1;
}

.form-bakhour > div{
  position:absolute;
  top:0;
  left:50%;
  padding:0 20px;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  background-color:black;
  border: 2px solid #ccc;
  border-radius: 20px;
  text-align:center;
}

.form-bakhour > div > img{
  -webkit-filter:invert(1);
          filter:invert(1);
  width:100px;
}

.form-bakhour > input,
.form-bakhour > textarea {
  padding: 12px;
  margin: 12px auto;
  border: 1px solid #ccc;
  color: #ddd;
  background: radial-gradient(
    45% 85% at 65% 65%,
    rgba(13, 58, 216, 0.8) 0%,
    rgba(0, 0, 0, 0) 100%
  ),
  radial-gradient(35% 70% at 35% 50%, #9d0303 0%, rgba(0, 0, 0, 0) 79.09%),
  #000000;
  border-radius: 4px;
  display: block;
  width: 70vw;
}

.form-bakhour > textarea{
  height:100px;
  resize:none;
}

.form-bakhour > input[type="submit"] {
  max-width: 20vw;
  padding-left: 1%;
  padding-right: 1%;
  cursor: pointer;
  -webkit-transition: 0.35s;
  transition: 0.35s;
}

.form-bakhour > input[type="submit"]:hover {
  background-color: #9d0303 ;
}

